import React, { useRef, useState, useEffect } from 'react';
import { Chart, registerables } from 'chart.js';
import { supabase } from '../config.js';

Chart.register(...registerables);
const banks = ['HDFC', 'ICICI', 'SBI' , 'KOTAK']; // replace with actual table names

const FDChart = () => {
  const fdChartRef = useRef(null);
  const fdChartInstance = useRef(null);

  const [principal, setPrincipal] = useState(10000);
  const [years, setYears] = useState(5);
  const [months, setMonths] = useState(1);
  const [days, setDays] = useState(20);
  const [bankRates, setBankRates] = useState([]);

  useEffect(() => {  
    updateChart();
  }, [principal, years, months, days]);

  const fetchRates = async (duration) => {
    let rates = [];
    for (let bank of banks) {
        const { data, error } = await supabase
                                        .from(bank)
                                        .select('rate')
                                        .lte("tenure_start", duration)
                                        .gte("tenure_end", duration)
                                        .limit(1);

      if (error) {
        console.error(`Error fetching data for ${bank}:`, error);
      } else {
        rates.push({ bank, rate: data?.[0]?.rate || 0 });

      }
    }
    return rates;
  };

  const updateChart = async () => {
    const tenureDays = years * 365 + months * 30 + days;
    const rates = await fetchRates(tenureDays);
    setBankRates(rates);
    generateFDChart(principal, rates, tenureDays);
  };

  const generateFDChart = (principal, rates, tenureDays) => {
    const ctx = fdChartRef.current.getContext('2d');
    if (fdChartInstance.current) fdChartInstance.current.destroy();

    const labels = [];
    const n = 4; // quarterly
    const tenureYears = tenureDays / 365;
    const tenureMonths = tenureDays / 30;
    const colors = ['blue', 'red', 'green', 'purple'];

    const datasets = rates.map(({ bank, rate }, index) => {
      const values = [];
      if (tenureDays <= 30) {
        for (let t = 0; t <= tenureDays; t++) {
          let amount = principal * Math.pow(1 + rate / (n * 100), n * (t / 365));
          if (index === 0) labels.push(t + ' days');
          values.push(amount.toFixed(2));
        }
      } else if (tenureDays <= 730) {
        for (let t = 0; t <= tenureMonths; t++) {
          let amount = principal * Math.pow(1 + rate / (n * 100), n * (t / 12));
          if (index === 0) labels.push(t + ' months');
          values.push(amount.toFixed(2));
        }
      } else {
        for (let t = 0; t <= tenureYears; t++) {
          let amount = principal * Math.pow(1 + rate / (n * 100), n * t);
          if (index === 0) labels.push(t + ' years');
          values.push(amount.toFixed(2));
        }
      }
      return {
        label: `${bank} - ${rate}%`,
        data: values,
        borderColor: colors[index % colors.length],
        borderWidth: 3,
        fill: false,
        fontSize:20,
        pointRadius: 5,
        pointHoverRadius: 8
      };
    });

    fdChartInstance.current = new Chart(ctx, {
      type: 'line',
      data: { labels, datasets },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          x: { title: { display: true, text: 'Tenure' } },
          y: { title: { display: true, text: 'Amount' } }
        },
        plugins: {
          tooltip: {
            mode: 'index',
            intersect: false,
            callbacks: {
              label: (tooltipItem) => {
                const { bank, rate } = rates[tooltipItem.datasetIndex];
                return `${bank} - ${rate}% : ${tooltipItem.raw}`;
              }
            }
          }
        },
        hover: {
          mode: 'index',
          intersect: false
        },
        onHover: () => {
          const maturityValues = rates.map(({ bank, rate }) => {
            const maturity = principal * Math.pow(1 + rate / (n * 100), n * tenureYears);
            return { bank, rate, maturity };
          });

          maturityValues.sort((a, b) => b.maturity - a.maturity);
          const highest = maturityValues[0];
          const totalInterest = highest.maturity - principal;

          document.getElementById('maturityAmount').innerText = `${highest.bank} Maturity Amount: ${highest.maturity.toFixed(2)}`;
          document.getElementById('interestAmount').innerText = `${highest.bank} Total Interest: ${totalInterest.toFixed(2)}`;
        }
      }
    });
  };


  const inputLabelStyle={
    textAlign:"center",
  }

  const deviceWidth = window.screen.availWidth;
  return (
    <div style={{minHeight:"650px",alignItems:"center", width:"100%"}}>
      <h2>Fixed Deposite Calculator</h2>
      <div style={{display:"flex", alignItems:"center" , justifyContent:"space-around", width:`${deviceWidth>700?"100%":String(deviceWidth*0.96)+"px"}`,flexDirection: deviceWidth<700?"column":"row", padding:"0 1%", width:"92%", ...{gap : deviceWidth>700?"25px":"5px"}}}>
        <div  >
            <label style={inputLabelStyle}><strong>Principal Amount</strong></label>
            <input style={{width:"100%", fontSize:"16px", borderRadius:"5px",border:"None",textAlign:"center"}} type="number" value={principal} onChange={e => setPrincipal(+Number(e.target.value))} placeholder="Principal" />
        </div>
        <div >  
            <label style={inputLabelStyle}><strong>Years</strong></label>
            <input style={{width:"100%",fontSize:"16px", borderRadius:"5px",border:"None",textAlign:"center"}} type="number" value={years} onChange={e => setYears(+Number(e.target.value))} placeholder="Years" />
        </div>
        <div>
            <label style={inputLabelStyle}><strong>Months</strong></label>     
            <input style={{width:"100%", fontSize:"16px", borderRadius:"5px",border:"None",textAlign:"center"}} type="number" value={months} onChange={e => setMonths(+Number(e.target.value))} placeholder="Months" />
        </div>
        <div>
            <label style={inputLabelStyle}><strong>Days</strong></label>
            <input style={{width:"100%", fontSize:"16px", borderRadius:"5px",border:"None",textAlign:"center"}} type="number" value={days} onChange={e => setDays(+Number(e.target.value))} placeholder="Days" />
        </div>
      </div>

      <div style={{ width: deviceWidth<700?'98%':"96%", height: '450px', disply:"flex", justifyContent:"center",margin:"1% 0",borderRadius:"12px",padding:"0 1%" , backgroundColor:"white" }}>
        <canvas ref={fdChartRef} ></canvas>
      </div>

      <div style={{display:"flex" , justifyContent:"space-around", fontStyle:"bold",flexDirection: window.screen.availWidth<700?"column":"row"}}>
      <p id="interestAmount"></p>
      <p id="maturityAmount"></p>
      </div>
    </div>
  );
};

export default FDChart;
