import React, { useState, useRef, useEffect } from "react";
import { marked } from "marked";
import "./Chat.css"; 

const API_URL =process.env.REACT_APP_SERVER_URL;
const Chat = () => {
  const [messages, setMessages] = useState([{from :"server",text:"Server May take 30sec to start."}]);
  const [input, setInput] = useState("");
  const chatBoxRef = useRef(null);

  useEffect(() => {
    if (chatBoxRef.current) {
      chatBoxRef.current.scrollTop = chatBoxRef.current.scrollHeight;
    }
  }, [messages]);

  const sendMessage = async () => {
    const message = input.trim();
    if (!message) return;

    setMessages((prev) => [...prev, { from: "user", text: `${message}` }]);
    setInput("");

    try {
      const payload = {
        text: message,
      };

      const response = await fetch(API_URL + "/resolve-query", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });

      const data = await response.json();
      console.log("contacting server...");

      const reply = data.response
        ? marked.parse(data.response)
        : marked.parse(data.error || "Unknown error");

      setMessages((prev) => [
        ...prev,
        { from: "bot", text: `${reply}` },
      ]);
    } catch (error) {
      console.error("Error sending message:", error);
      setMessages((prev) => [
        ...prev,
        { from: "bot", text: "Server Error. Try again." },
      ]);
    }
  };

  const startSpeechRecognition = () => {
    const SpeechRecognition =
      window.SpeechRecognition || window.webkitSpeechRecognition;
    if (!SpeechRecognition) {
      alert("Speech Recognition is not supported in your browser.");
      return;
    }

    const recognition = new SpeechRecognition();
    recognition.lang = "en-US";
    recognition.continuous = false;
    recognition.interimResults = false;

    recognition.onresult = (event) => {
      const transcript = event.results[0][0].transcript;
      console.log("Transcript:", transcript);
      setInput(transcript);
    };

    recognition.onerror = (event) => {
      console.error("Speech recognition error:", event.error);
      alert("Speech recognition error: " + event.error);
    };

    recognition.onspeechend = () => {
      recognition.stop();
      console.log("Speech recognition ended.");
    };

    recognition.start();
  };


  const deviceWidth = window.screen.availWidth ; 
  
  const chatBoxStyle = {
    height: `${window.screen.availHeight-300}px`,
    overflowY: "scroll",
    overflowX: "clip",
    border: "1px solid #ccc",
    padding: "10px",
    border: "hidden",
    display: "flex",
    flexDirection: "column",
    maxWidth:"550px",
    background:"white",
    backdropFilter: "8px",
    borderRadius:"8px",
    width: `${ deviceWidth > 700 ? 550 : deviceWidth/2}`
  };


  async function greetingCall(){

    const response = await fetch(API_URL + "/", {
          method: "GET",
          headers: { "Content-Type": "application/json" },
        }).catch(e => {console.log("error Connecting")})
        ; 
    }
  
  window.addEventListener("load", ()=>{greetingCall();})

  const chatParentStyle={
    minWidth : deviceWidth>700?"550px":`${deviceWidth*0.65}`,
  }
  const chatContainerStyle={
    minHeight : `${window.screen.availHeight+150}px`
  }

  return (
    <div style={chatParentStyle}  onKeyDown={(e)=>{
            if ((window.screen.availWidth > 700) & (e.key === "Enter")) sendMessage();
          }}>
      <h2>Chat Window</h2>
      <div className="chat-container" style = {chatContainerStyle}>
        <div className="chat-box" ref={chatBoxRef} style={chatBoxStyle}>
          {messages.map((msg, index) => (
            <div
              key={index}
              className={`chat-message ${
                msg.from === "user" ? "user-message" : "server-message"
              }`}
              dangerouslySetInnerHTML={{ __html: msg.text }}
            ></div>
          ))}
        </div>
        <div className="chat-input" style={{ marginTop: "10px" , transform:"translateY(20px)"}}>
          <input
            id="text-input"
            type="text"
            value={input}
            onChange={(e) => setInput(e.target.value)}
            placeholder="Type a message..."
            style={{ width: "60%", padding: "5px" }}
          />
          <div className="buttons" >

          <button id="send" onClick={sendMessage} style={{ marginLeft: "5px" }}>
            Send
          </button>
          <button id="mic"
            onClick={startSpeechRecognition}
            style={{ marginLeft: "5px" }}
            >
            
          </button>
            </div>
        </div>
      </div>
    </div>
  );
};

export default Chat;
